@use 'variables' as v;

/** Media Queries **/
@mixin phone {
	@media (max-width: v.$phone) {
			@content;
	}
}
@mixin tablet {
	@media (max-width: v.$tablet) {
			@content;
	}
}
@mixin desktop {
	@media (max-width: v.$desktop) {
			@content;
	}
}

@mixin breakpointDesktop {
	@media (max-width: v.$breakpointDesktop) {
			@content;
	}
}

@mixin breakpointPhone {
	@media (max-width: v.$breakpointPhone) {
			@content;
	}
}

@mixin breakpointSmartWatch {
	@media (max-width: v.$breakpointSmartWatch) {
			@content;
	}
}


@mixin button($bgColor, $textColor) {
	background-color: $bgColor;
	padding: 1.5rem 4rem;
	color: $textColor;
	margin-top: 2rem;
	font-size: 2rem;
	font-weight: 400;
	display: inline-block;
	font-weight: 700;
	border: none;
	border-radius: .5rem;
	transition-property: background-color;
	transition-duration: .15s;
	text-align: center;

	@include tablet {
		width: auto;
	}
	&:hover {
		background-color: darken( $bgColor, 10% );
		cursor: pointer;
	}
}